import '@availity/favorites/style.scss';
import README from '@availity/favorites/README.md';
import '@availity/mock';

storiesOf('Icons|Favorite', module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } })
